<template>
    <div>
        <h4>文章填写</h4>
        <div class="logBox">
            <ul>
                <li>
                    <label for="title">文章标题</label>
                    <input name="title" type="text" v-model="title" placeholder="请输入文章标题"/>
                </li>
                <li>
                    <label for="author">文章作者</label>
                    <input name="author" type="text" v-model="author" placeholder="请输入作者"/>
                </li>
                <li style="height:auto">
                    <label for="content">文章内容</label>
                    <textarea name="content" type="content" v-model="content" placeholder="请输入文章内容"/>
                </li>
            </ul>
        </div>
        <button type="button" @click="addfeel()">添加文章</button>

    </div>

</template>

<script>

export default {
    data(){
        return{
            title:'',
            author:'',
            content:'',
            time:'',
            items:[]
        }
    },
    methods:{
        addfeel(){
            const vm = this;
            this.axios.addFeel({
                title: vm.title,
                author:vm.author,
                content:vm.content,
                time:vm.time,
            }).then(res=>{
                console.log(res)
                this.Toast(res.returnMsg,()=>{
                    this.$router.push('/waterList')
                })
            })
        },
        query(){
            this.time = new Date();
            console.log(this.time)
        }
    },
    mounted() {
        this.query()
    },
}
</script>




